// sass-lint:disable no-important

// Top spacing
.spacing-top-flush {
  margin-top: 0 !important;
}

.spacing-top-double {
  margin-top: 2 * $gutter !important;
}

.spacing-top-single {
  margin-top: $gutter !important;
}

.spacing-top-half,
.spacing-top-one-half {
  margin-top: $gutter-half !important;
}

.spacing-top-third,
.spacing-top-one-third {
  margin-top: $gutter-one-third !important;
}


